import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    fieldData: {
      city: null,
      state: null,
      country: null,
      height: 68
    },
    currentStep: 0,
    formConfig: {
      steps: [
        {
          messageText1: 'New FDA approved, non surgical weight loss.',
          messageText2: 'See if you qualify for $1500 off!',
          buttonText: 'Continue',
          components: [
            {
              id: '1',
              type: 'DynamicInputNumberv01',
              dynamicProps: {
                focus: true,
                label: 'Enter your weight in lbs',
                dataName: 'weight',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '2',
              type: 'DynamicInputSelectv01',
              dynamicProps: {
                label: 'Select your height',
                dataName: 'height',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '3',
              type: 'DynamicInputZipcodev01',
              dynamicProps: {
                label: 'Enter your zipcode',
                dataName: 'zipcode',
                style: 'font-size: 2rem'
              }
            }
          ]
        },
        {
          messageText1: 'Lets check for a provider in your area.',
          messageText2: '&nbsp;',
          buttonText: 'Next',
          components: [
            {
              id: '1',
              type: 'DynamicInputTextv01',
              dynamicProps: {
                focus: true,
                label: 'Street address',
                dataName: 'address',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '2',
              type: 'DynamicInputTextv01',
              dynamicProps: {
                label: 'City',
                dataName: 'city',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '3',
              type: 'DynamicInputTextv01',
              dynamicProps: {
                label: 'State',
                dataName: 'state',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '4',
              type: 'DynamicInputZipcodev01',
              dynamicProps: {
                label: 'Zipcode',
                dataName: 'zipcode',
                style: 'font-size: 2rem'
              }
            }
          ]
        },
        {
          messageText1: 'Great news! Last step.',
          messageText2: '&nbsp;',
          buttonText: 'I\'m ready to start losing',
          components: [
            {
              id: '1',
              type: 'DynamicInputTextv01',
              dynamicProps: {
                focus: true,
                label: 'First name',
                dataName: 'firstName',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '2',
              type: 'DynamicInputTextv01',
              dynamicProps: {
                label: 'Last name',
                dataName: 'lastName',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '3',
              type: 'DynamicInputEmailv01',
              dynamicProps: {
                label: 'Email address',
                dataName: 'email',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '4',
              type: 'DynamicInputPhonev01',
              dynamicProps: {
                label: 'Phone number',
                dataName: 'phone',
                style: 'font-size: 2rem'
              }
            },
            {
              id: '5',
              type: 'DynamicInputCheckboxv01',
              dynamicProps: {
                label: 'By submitting this form I agree to be contacted and to HIPPA waiver.',
                dataName: 'agreeTerms',
                style: 'font-size: large; text-color: red;'
              }
            }
          ]
        }
      ]
    }
  },
  getters: {
    currentStep (state) {
      return state.formConfig.steps[state.currentStep]
    },
    getFieldData (state) {
      return state.fieldData
    },
    progress (state) {
      var progress = Math.floor((state.currentStep + 1) / state.formConfig.steps.length * 100)
      return progress + '%'
    },
    hasPrevious (state) {
      return state.currentStep > 0
    },
    hasNext (state) {
      return (state.currentStep + 1) < state.formConfig.steps.length
    }
  },
  mutations: {
    updateFieldData (state, payload) {
      state.fieldData[payload.dataName] = payload.value
    },
    nextStep (state) {
      state.currentStep++
    },
    prevStep (state) {
      state.currentStep--
    }
  }
})



