<template>
  <div class="input-field">
    <label v-bind:class="{ active: fieldDataValue }">{{dynamicProps.label}}</label>
    <input
      type="tel" class="validate"
      v-bind:style="dynamicProps.style"
      @change="localValueChanged" :value="fieldDataValue"
      v-bind:data-focus="dynamicProps.focus" v-focus>
  </div>
</template>

<script>
import { dynamicInputMixin } from '../mixins'
export default {
  name: 'dynamic-input-zipcode-v01',
  mixins: [dynamicInputMixin],
  props: ['dynamicProps'],
  methods: {
    localValueChanged (ev) {
      this.lookupZip(ev.target.value)
      this.valueChanged(ev)
    },
    lookupZip (zip) {
      var url = this.generateOpenCageDataUrlZip(zip)

      fetch(url)
      .then((response) => {
        return response.json()
      })
      .then((location) => {
        var result = location.results[0].components
        console.log(result)
        this.$store.commit('updateFieldData', {
          dataName: 'city',
          value: result.city
        })
        this.$store.commit('updateFieldData', {
          dataName: 'state',
          value: result.state
        })
        this.$store.commit('updateFieldData', {
          dataName: 'country',
          value: result['ISO_3166-1_alpha-2']
        })
      })
    },
    generateOpenCageDataUrlZip (zip) {
      var key = '41886799cfe34e87af44a27cf6ab3107'
      return 'https://api.opencagedata.com/geocode/v1/json?key=' + key + '&q=' + zip + '&countrycode=us&no_annotations=1&limit=1'
    }
  }
}
</script>

<style>
</style>



