<template>
  <div>

    <div>
      <p class="flow-text center-align" v-html="messageText1"></p>
      <p class="flow-text center-align" v-html="messageText2"></p>
    </div>

    <div class="progress">
      <div class="determinate" :style="{width: progress}"></div>
    </div>

    <form @submit.prevent="next">

      <component
        v-for="component in components"
        v-bind:key="component.id"
        v-bind:is="component.type"
        v-bind:dynamicProps="component.dynamicProps"/>

      <div class="input-field">
        <div class="row">
          <div class="col s2">
            <button v-show="hasPrevious" @click.prevent="previous" class="btn waves-effect waves-green grey">back</button>
          </div>
          <div class="col s8 center-align">
            <button type="submit" class="btn waves-effect waves-teal" v-html="buttonText"></button>
          </div>
        </div>
      </div>

    </form>

  </div>  
</template>

<script>
import DynamicInputCheckboxv01 from './DynamicInputCheckboxv01'
import DynamicInputEmailv01 from './DynamicInputEmailv01'
import DynamicInputPhonev01 from './DynamicInputPhonev01'
import DynamicInputNumberv01 from './DynamicInputNumberv01'
import DynamicInputSelectv01 from './DynamicInputSelectv01'
import DynamicInputTextv01 from './DynamicInputTextv01'
import DynamicInputZipcodev01 from './DynamicInputZipcodev01'

export default {
  name: 'dynamic-step',
  components: {
    DynamicInputCheckboxv01,
    DynamicInputEmailv01,
    DynamicInputPhonev01,
    DynamicInputNumberv01,
    DynamicInputSelectv01,
    DynamicInputTextv01,
    DynamicInputZipcodev01
  },
  computed: {
    messageText1 () {
      return this.$store.getters.currentStep.messageText1
    },
    messageText2 () {
      return this.$store.getters.currentStep.messageText2
    },
    components () {
      return this.$store.getters.currentStep.components
    },
    buttonText () {
      return this.$store.getters.currentStep.buttonText
    },
    progress () {
      return this.$store.getters.progress
    },
    hasPrevious () {
      return this.$store.getters.hasPrevious
    },
    hasNext () {
      return this.$store.getters.hasNext
    }
  },
  methods: {
    next () {
      if (this.hasNext) {
        this.$store.commit('nextStep')
      } else {
        this.$router.push({ name: 'thankyou' })
      }
    },
    previous () {
      if (this.hasPrevious) {
        this.$store.commit('prevStep')
      }
    }
  }
}
</script>

<style>
</style>



