<template>
  <div>
    <a href="#" @click.prevent="valueChanged">
      <i class="material-icons left grey-text">{{fieldDataValue}}</i>
    </a>
    <label>
      <span class="flow-text" v-bind:style="dynamicProps.style">{{dynamicProps.label}}</span>
    </label>        
  </div>
</template>

<script>
export default {
  name: 'dynamic-input-checkbox-v01',
  props: ['dynamicProps'],
  data () {
    return {
      boxChecked: false
    }
  },
  created () {
    this.boxChecked = this.$store.getters.getFieldData[this.dynamicProps.dataName]
  },
  computed: {
    fieldDataValue () {
      return this.boxChecked ? 'check_box' : 'check_box_outline_blank'
    }
  },
  methods: {
    valueChanged (ev) {
      this.boxChecked = ev.target.innerText === 'check_box_outline_blank'
      this.$store.commit('updateFieldData', {
        dataName: this.dynamicProps.dataName,
        value: this.boxChecked
      })
    }
  }
}
</script>

<style>
</style>



