export const dynamicInputMixin = {
  computed: {
    fieldDataValue () {
      return this.$store.getters.getFieldData[this.dynamicProps.dataName]
    }
  },
  methods: {
    valueChanged (ev) {
      console.log(ev)
      this.$store.commit('updateFieldData', {
        dataName: this.dynamicProps.dataName,
        value: ev.target.value
      })
    }
  }
}



