import Vue from 'vue'
import Router from 'vue-router'
import Thankyou from '@/components/Thankyou'
import DynamicStep from '@/components/DynamicStep'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dynamic',
      component: DynamicStep
    },
    {
      path: '/thankyou',
      name: 'thankyou',
      component: Thankyou
    }
  ]
})



